/*----------------------------------------*/
/*  Offcanvas Header Css
/*----------------------------------------*/

.off-canvas-wrapper {
	&.offcanvas {
		background-color: #fff;
		width: 310px;
		z-index: 9999;
		@media #{$tablet-device, $large-mobile} {
			width: 310px;
		}
	}
	.offcanvas-header {
		padding: 0;
	}
	.offcanvas-body {
		display: flex;
		width: 100%;
		justify-content: space-between;
		flex-direction: column;
		padding: 0;
		scrollbar-width: auto;
		scrollbar-color: #1f1f1f #292929;
		/* Chrome, Edge, and Safari */
		&::-webkit-scrollbar {
			width: 2px;
		}
		&::-webkit-scrollbar-track {
			background: #292929;
		}
		&::-webkit-scrollbar-thumb {
			background-color: #1f1f1f;
			border-radius: 2px;
			border: 2px solid #1f1f1f;
		}
	}
	.mobile-menu-action {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 100%;
		padding-bottom: 20px;
	}
	.header-social-icons {
		padding: 20px;
		a {
			+ {
				a {
					margin-left: 14px;
				}
			}
		}
		img {
			filter: brightness(0.2);
			opacity: 0.7;
			width: 24px;
			&:hover {
				opacity: 1;
			}
		}
	}
	.btn-menu-close {
		display: block;
		width: 100%;
		height: 60px;
		background-color: $primary;
		opacity: 1;
		border-radius: 0;
		color: $white;
		background-image: none;
		padding: 0 20px;
		line-height: 62px;
		font-size: 16px;
		text-transform: uppercase;
		font-weight: $font-weight-semi-bold;
		text-align: left;
		border: none;
		position: relative;
		@include transition(all 0.3s ease-out);

		i {
			position: absolute;
			right: 21px;
			top: 50%;
			transform: translate(0%, -50%);
		}

		&:hover {
			color: $white;
			background-color: #2f2f2f;
		}
	}
}

// Offcanvas Menu
.mobile-menu-items {
	padding: 0;

	& ul {
		border-top: 1px solid rgba(10, 77, 60, 0.15);

		& li {
			position: relative;

			& a {
				display: block;
				width: 100%;
				background: none;
				text-align: left;
				padding: 10px 20px;
				border: 0;
				border-bottom: 1px solid rgba(10, 77, 60, 0.15);
				font-size: 15px;
				font-weight: 600;
				font-family: $black;
				color: $black;
				&:hover {
					color: $primary;
				}
			}

			& .mobile-menu-expand {
				position: absolute;
				right: 20px;
				top: 18px;
				width: 12px;
				height: 12px;
				cursor: pointer;

				&::before {
					position: absolute;
					content: "";
					width: 100%;
					height: 1px;
					background-color: $black;
					top: 50%;
					transform: translateY(-50%);
				}

				&::after {
					position: absolute;
					content: "";
					width: 1px;
					height: 100%;
					background-color: $black;
					left: 50%;
					bottom: 0;
					transform: translateX(-50%);
				}
			}

			&.active-expand {
				& > .mobile-menu-expand {
					&::before {
						background-color: $primary;
					}
					&::after {
						background-color: $primary;
						height: 0;
					}
				}
				& a {
					color: $primary;
				}
				& ul {
					& li {
						& a {
							color: #272a33;
						}
					}
				}
			}

			ul {
				border-top: 0;

				& li {
					& a {
						padding-left: 12%;
						&:hover {
							color: $primary;
						}
					}

					ul {
						& li {
							& a {
								padding-left: 18%;
							}
						}
					}
				}
			}
		}
	}
}

.btn-menu {
	background: none;
	border: none;
	height: auto;
	line-height: 1;
	padding: 0;
	position: relative;
	margin: 0;
	gap: 8px;
	display: flex;
	width: 30px;
	flex-direction: column;

	& span {
		display: inline-block;
		background-color: $primary;
		width: 100%;
		height: 2px;
	}
}

.mobile-menu-info-list {
	margin-bottom: 0;
	li {
		padding: 6px 20px;

		.icon {
			color: $primary;
			font-size: 18px;
			position: relative;
			top: 2px;
			margin-right: 8px;
		}

		a,
		span {
			color: $dark;
			font-size: 14px;
			font-weight: 400;
		}

		a {
			&:hover {
				color: $primary;
			}
		}

		&:last-child {
			border-bottom: none;
		}
	}
}

.mobail-header-donate-btn {
	padding: 9px 17px 8px !important;
	width: 100%;
}
