// Font Family
$roboto: "Roboto", sans-serif;

// Responsive Variables
$extraBig-device: "only screen and (min-width: 1550px)";
$laptop-device: "only screen and (min-width: 1200px) and (max-width: 1549px)";
$desktop-device: "only screen and (min-width: 992px) and (max-width: 1199px)";
$tablet-device: "only screen and (min-width: 768px) and (max-width: 991px)";
$large-mobile: "only screen and (max-width: 767px)";
$small-mobile: "only screen and (max-width: 575px)";
$extra-small-mobile: "only screen and (max-width: 479px)";

//===============================
// Bootstrap Variables Overright
//===============================

// Color
$primary: #fed500;
$secondary: #391c08;
$success: #4caf50;
$danger: #f44336;
$warning: #ffc107;
$info: #17a2b8;
$light: #f7f4ef;
$dark: #131b13;
//
$body-color: #383e38;
$headings-color: #391c08;
//
$white: #ffffff;
$black: #000000;
$grey: #f7f4ef;

// theme colors map
$theme-colors: (
	"primary": $primary,
	"secondary": $secondary,
	"success": $success,
	"danger": $danger,
	"warning": $warning,
	"info": $info,
	"light": $light,
	"dark": $dark,
);

// Breadcrumb
$breadcrumb-active-color: $primary;

// Options
$enable-negative-margins: true;
// Spacing
$spacer: 1rem;
$spacers: (
	0: 0,
	1: $spacer * 0.3125,
	2: $spacer * 0.625,
	3: $spacer * 0.9375,
	4: $spacer * 1.25,
	5: $spacer * 1.625,
	6: $spacer * 1.875,
	7: $spacer * 2.1875,
	8: $spacer * 2.5,
	9: $spacer * 2.8125,
	10: $spacer * 3.125,
);
// Grid breakpoints
$grid-breakpoints: (
	xs: 0,
	sm: 576px,
	md: 768px,
	lg: 992px,
	xl: 1200px,
	xxl: 1600px,
);

// Grid containers
$container-max-widths: (
	sm: 540px,
	md: 720px,
	lg: 960px,
	xl: 1200px,
	xxl: 1200px,
);
//
$grid-gutter-width: 30px;
//
$gutters: $spacers;
// Transition
$transition-base: all 0.5s ease 0s;

// Font Weight
$font-weight-light: 300;
$font-weight-normal: 400;
$font-weight-medium: 600;
$font-weight-semi-bold: 600;
$font-weight-bold: 700;
$font-weight-extra-bold: 800;
$font-weight-black: 900;

// Typography
$font-family-base: $roboto;
$font-size-base: 1.0625rem;
$font-weight-base: $font-weight-normal;
$line-height-base: 1.883;
//
$headings-font-family: $roboto;
$headings-font-weight: $font-weight-medium;
$headings-line-height: 1.2;
