@import "./01-helpers/index";
@import "./bootstrap/bootstrap";
@import "./02-basic/index";
@import "./03-layout/index";
@import "./04-modules/index";

// Plugins
@import "./plugins/fancybox.min";
@import "./plugins/icofont.min";
@import "./plugins/swiper-bundle.min";

.ylabel {
	text-transform: uppercase;
	background-color: $primary;
	color: $secondary;
	letter-spacing: 2.2px;
	padding: 19px 20px 16px 20px;
	width: 264px;
	display: inline-block;
	font-size: 15px;
	line-height: 1;
	margin-bottom: 10px;
	&.sm {
		width: 224px;
		font-size: 13px;
		padding: 11px 15px;
		width: 183px;
	}
}

.story-wrap {
	text-align: center;
	margin-bottom: 30px;
	@include media-breakpoint-up(lg) {
		margin-top: -170px;
		margin-bottom: 60px;
	}
}

.header-donate-btn {
	color: $secondary;
}

.header-top-lang {
	li {
		margin-right: 10px;
		font-size: 15px;
		height: 29px;
		width: 29px;
		display: flex;
		align-items: center;
		justify-content: center;
		border-radius: 100%;
		&.active {
			background-color: $primary;
		}
	}
}

.track {
	position: relative;
	.van-wrap {
		transition: all 100ms ease-in-out;
		position: absolute;
		left: 0;
		bottom: 0;
		width: 100%;
		height: 100%;
	}
	&-van {
		transition: all 100ms ease-in-out;
		position: absolute;
		bottom: 0;
		left: 0;
	}
	&-frame {
	}
	.flag {
		position: absolute;
		right: -3.5%;
		bottom: 95%;
	}
	.goal {
		font-size: 11px;
		position: absolute;
		right: 1.8%;
		bottom: 116%;
		line-height: 1.1;
		span {
			font-size: 15px;
			font-weight: bold;
		}
	}
	.raised {
		font-size: 11px;
		line-height: 1.1;
		margin: -7px 0 0 20px;
		span {
			font-size: 15px;
			font-weight: bold;
		}
	}
	.track-frame {
		position: absolute;
		bottom: -29%;
		left: 3.5%;
	}
}

.text-gray {
	color: #7b7b7b !important;
}
