/*----------------------------------------*/
/* Common Style */
*, *::after, *::before {
  box-sizing: border-box;
}

html, body {
  height: 100%;
}

body {
  font-family: "Roboto", sans-serif, Arial, Helvetica, sans-serif;
  font-size: 1.0625rem;
  font-weight: 400;
  font-style: normal;
  line-height: 1.883;
  position: relative;
  visibility: visible;
  overflow-x: hidden;
  color: #383e38;
  background-color: #ffffff;
