  box-shadow: none;
  outline: none;
  text-decoration: none;
}

a, button, img, input {
  transition: all 0.5s ease 0s;
}

*:focus {
  outline: none;
}

a:focus {
  text-decoration: none;
  color: inherit;
  outline: none;
}

a:hover {
